<template>
<br>
<div class="view container-fluid baseWidth" style="text-align:center">
  <div style="text-align:left">
    <h4 class="fontBase">{{this.stData.title}}</h4>
    <div class="row">
      <div class="col" style="text-align:left">
        <h6 class="fontBase">配信者：{{this.stData.user_name}}</h6>
      </div>
      <div class="col" style="text-align:right">
        <h6 class="fontBase"><img :src="this.$root.IMG_PATH + 'icon_view.png'" style="width:20px">{{this.stData.view_count}}人が視聴中</h6>
      </div>
    </div>
  </div>
  <hr style="'margin:3px 0px; height: 2px; background-color:white; border:none; margin:auto'">
  <div class="row" style="justify-content:center; margin:auto">
    <iframe 
      class="iframe"
      id="iframe"
      src="" 
      frameborder="0" 
      allowfullscreen="true" 
      scrolling="no" 
      width="100%" height="100%"></iframe>
  </div>
  <hr style="'margin:0px auto 10px; height: 2px; background-color:white; border:none;'">
  <div v-show="this.$root.mode == 0">
    <div class="row">
      <div class="col-6 col-md-4" style="text-align:left; padding:0">
        <h5 class="fontBase" style="margin-left:0"><span><span >次の配信まで</span>{{this.timerVal}}秒</span></h5>
      </div>
      <div class="col-6 col-md-4" style="text-align:center;">
        <div v-show="this.viewCount != 0">
          <h5 class="fontBase">ワンツイ視聴者：{{this.viewCount}}人</h5>
        </div>
      </div>
      <div class="col-12 col-sm-4" style="text-align:right; padding:0">
        <img :src="this.$root.IMG_PATH + 'btn_transition.png'" style="height:4rem" v-on:click="pushStream(true)">
      </div>
    </div>
    <h5 v-show="showNext" class="fontBase">まもなく次の配信「{{this.nextData.title}}」</h5>
    <h5 v-show="!showNext" class="fontBase">&nbsp;</h5>
  </div>
  <div v-show="this.$root.mode == 1">
    <div class="row justify-content-md-center">
      <div class="col-12 col-md-5" style="text-align:center; padding:0">
        <img :src="this.$root.IMG_PATH + 'btn_skip.png'" style="height:4rem" v-on:click="pushSkip()">
      </div>
      <div class="col-12 col-md-5" style="text-align:center; padding:0">
        <img :src="this.$root.IMG_PATH + 'btn_transition.png'" style="height:4rem" v-on:click="pushStream(true)">
      </div>
    </div>
  </div>
  <!-- <button type="button" v-on:click="pushHome" class="btn btn-outline-primary">ゲーム一覧へ戻る</button> -->
  <br>
  <div v-show="this.lastData.user_id != ''">
    <h5 class="fontBase">前回の配信</h5>
    <hr :style="'margin:0px auto 10px; height: 2px; background-color:white; border:none; margin:auto'">
    <div class="row justify-content-md-center">
      <div class="col-auto">
        <img :src="lastIcon" style="width:170px; height:96px">
      </div>
      <div class="col">
        <h5 class="fontBase">{{this.lastData.title}}</h5>
        <h5 class="fontBase">{{this.lastData.user_name}}</h5>
      </div>
      <div class="col-auto" style="text-align:right">
        <img :src="this.$root.IMG_PATH + 'btn_transition.png'" style="width:150px" v-on:click="pushStream(false)">
      </div>
    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'View',
  components: {
  },
  data() {
    return {
      stWidth: 900,
      gameId : "",
      userId : "",
      currentId : "",
      stUrl : "",
      stUrl2 : "https://player.twitch.tv/?channel=fps_shaka&parent=localhost",
      showNext : false,
      timerVal : 99,
      viewCount : 0,

      allStreamData : [],

      lastData : {
        user_id : "",
        user_name : "",
        title : "",
        view_count : "",
        icon : "",
      },

      stData : {
        user_id : "",
        user_name : "",
        title : "",
        view_count : "",
        icon : "",
      },

      nextData : {
        user_id : "",
        user_name : "",
        title : "",
        view_count : "",
        icon : "",
      },
      
      timerID : null,
    };
  },
  computed : {
    stHeight : function () {
      return this.stWidth / 1.6;
    },
    lastIcon : function () {
        let iconUrl = this.lastData.icon;
        iconUrl = iconUrl.replace('{width}', '340');
        iconUrl = iconUrl.replace('{height}', '192');
        return iconUrl;
    }
  },
  methods : {
    pushHome : function () {
      this.$router.push("/");
    },
    initAction : function () {
      this.gameId = this.$route.params.gameId;
      this.getStream(true);
    },
    pushStream : function (isCurrent) {
      if (isCurrent) {
        //現在の配信を開く
        window.open('https://www.twitch.tv/' + this.stData.user_id, '_blank');
      } else {
        //ひとつ前の配信を開く
        window.open('https://www.twitch.tv/' + this.lastData.user_id, '_blank');
      }
    },
    pushSkip : function () {
      let ran = -1;
      while (ran == -1) {
        if (this.allStreamData.length == 1) {
          ran = 0;
        } else {
          ran = Math.floor(Math.random() * this.allStreamData.length);
          let data = this.allStreamData[ran];

          if (data["user_id"] == this.stData.user_id) {
            ran = -1;
          }
        }
      }
      let nextData = this.allStreamData[ran];
      this.lastData.user_id = this.stData.user_id;
      this.lastData.user_name = this.stData.user_name;
      this.lastData.title = this.stData.title;
      this.lastData.view_count = this.stData.view_count;
      this.lastData.icon = this.stData.icon;

      //最新情報に詰め替え
      this.stData.user_id = nextData.user_id;
      this.stData.user_name = nextData.user_name;
      this.stData.title = nextData.title;
      this.stData.view_count = nextData.view_count;
      this.stData.icon = nextData.icon;
      document.getElementById("iframe").contentWindow.location.replace("https://player.twitch.tv/?channel=" + this.stData.user_id + "&parent=" + this.$root.DOMAIN_PATH);
    },
    updateTime : function () {
      //現在時刻取得
      var date = new Date();
      this.timerVal = 60 - date.getSeconds();
      console.log(this.timerVal);
      if (this.timerVal == 30) {
        this.getStream(false);
      } else if (this.timerVal <= 15 && this.$root.mode == 0) {
        if (this.stData.user_id != this.nextData.user_id) {
          this.showNext = true;
        }
      } else if (this.timerVal == 60 && this.$root.mode == 0) {
        this.changeStream();
      }
    },
    changeStream : function () {      
      this.showNext = false;

      //前回情報に詰め替え
      this.lastData.user_id = this.stData.user_id;
      this.lastData.user_name = this.stData.user_name;
      this.lastData.title = this.stData.title;
      this.lastData.view_count = this.stData.view_count;
      this.lastData.icon = this.stData.icon;

      //最新情報に詰め替え
      this.stData.user_id = this.nextData.user_id;
      this.stData.user_name = this.nextData.user_name;
      this.stData.title = this.nextData.title;
      this.stData.view_count = this.nextData.view_count;
      this.stData.icon = this.nextData.icon;
      document.getElementById("iframe").contentWindow.location.replace("https://player.twitch.tv/?channel=" + this.stData.user_id + "&parent=" + this.$root.DOMAIN_PATH);
      // this.stUrl = "https://player.twitch.tv/?channel=" + this.stData.user_id + "&parent=" + this.$root.DOMAIN_PATH;
    },
    getStream : function (isInit) {
      console.log("最新取得");
      let params = new URLSearchParams();
      params.append('userId', this.$root.userId);
      params.append('gameId', this.gameId);
      axios.post(this.$root.SERVER_PATH + 'getStreams', params)
      // axios.get(this.$root.ROOT_PATH + "storage/" + this.gameId + ".txt")
      .then((response) => {
        console.log(response.data);
        let data = response.data["data"];
        // let data = response.data;

        const jsonData = JSON.parse(JSON.stringify(data));

        if (this.$root.mode == 0) {
          //みんなで視聴モード
          let keys = Object.keys(jsonData);
          //現在日時のキー
          var date = new Date();
          const formatDate = (current_datetime)=>{
              var yyyy = current_datetime.getFullYear();
              var mm = ("0" + (current_datetime.getMonth() + 1)).slice(-2);
              var dd = ("0" + current_datetime.getDate()).slice(-2);
              var hh = ("0" + current_datetime.getHours()).slice(-2);
              var mi = ("0" + current_datetime.getMinutes()).slice(-2);
              let formatted_date = yyyy + "-" + mm + "-" + dd + "-" + hh + "-" + mi;
              return formatted_date;
          }
          var nextDate = new Date();
          nextDate.setMinutes(nextDate.getMinutes() + 1);

          let currentKey = formatDate(date);
          let nextKey = formatDate(nextDate);

          for (let i = 0; i < keys.length; i++) {
            const data = jsonData[keys[i]];

            if (isInit) {
              if (currentKey == keys[i]) {
                //現在の配信を設定
                this.stData.user_id = data["user_id"];
                this.stData.user_name = data["user_name"];
                this.stData.title = data["title"];
                this.stData.view_count = data["view_count"];
                this.stData.icon = data["icon"];
                document.getElementById("iframe").contentWindow.location.replace("https://player.twitch.tv/?channel=" + this.stData.user_id + "&parent=" + this.$root.DOMAIN_PATH);
                // this.stUrl = "https://player.twitch.tv/?channel=" + this.stData.user_id + "&parent=" + this.$root.DOMAIN_PATH;
              }
            }
            if (nextKey == keys[i]) {
              this.nextData.user_id = data["user_id"];
              this.nextData.user_name = data["user_name"];
              this.nextData.title = data["title"];
              this.nextData.view_count = data["view_count"];
              this.nextData.icon = data["icon"];
            }
          }

          //アクティブ視聴者数
          this.viewCount = data["view"];
          if (this.viewCount == 0) {
            this.viewCount = 1;
          }
        } else {
          //ザッピングモード
          this.allStreamData = jsonData["allList"];
          if (isInit) {
            let ran = Math.floor(Math.random() * this.allStreamData.length);
            let data = this.allStreamData[ran];
            this.stData.user_id = data["user_id"];
            this.stData.user_name = data["user_name"];
            this.stData.title = data["title"];
            this.stData.view_count = data["view_count"];
            this.stData.icon = data["icon"];
            document.getElementById("iframe").contentWindow.location.replace("https://player.twitch.tv/?channel=" + this.stData.user_id + "&parent=" + this.$root.DOMAIN_PATH);
          }
        }

        if (isInit) {
          //タイマーをスタート
          this.timerID = setInterval(this.updateTime, 1000);
        }
      }).catch((error) => {
        console.log(error.response);
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.timerID);
    next();
  },
}
</script>
