import { createApp } from 'vue/dist/vue.esm-bundler';
import App from './App.vue'
import router from './router'
import axios from 'axios'

const gameTitleRow = {
    props:["rowData"],
    template : `
	<div class="col-6 col-md-4 col-lg-3 col-xl-2">
        <div class="card" style="width:99%; height:95%">
			<router-link :to="'/view/' + this.rowData.id" >
				<img :src="iconUrl" class="card-img-top" alt="..." style="border-bottom:1px solid gray">
				<div class="card-body" style="text-align:center">
					<h6 class="card-title" style="height:3rem" >{{ this.rowData.name }}</h6>
					<h6 class="card-subtitle mb-2 text-muted">配信数：{{ this.rowData.order }}</h6>
					<div v-show="this.rowData.view != 0">
						<h6 class="card-subtitle mb-2 text-muted">
							<img :src="this.$root.IMG_PATH + 'icon_onetwi.png'" style="width:1rem">:{{ this.rowData.view }}
						</h6>
					</div>
					<div v-show="this.rowData.view == 0">
						<h6 class="card-subtitle mb-2 text-muted">
							&nbsp;
						</h6>
					</div>
				</div>
			</router-link>
        </div>
	</div>`,
	data: function () {
			return {
			}
	},
	computed : {
		iconUrl : function () {
            let iconUrl = this.rowData.icon;
            iconUrl = iconUrl.replace('{width}', '200');
            iconUrl = iconUrl.replace('{height}', '266');
			return iconUrl;
		},
	},
	methods : {
		pushGame (gameId) {
			console.log(gameId);
			this.$router.push('/view/' + gameId);
		}
	}
}


createApp(App)
.component("game-title-row", gameTitleRow)
.use(router, axios)
.mount('#app')
