<template>
<br>
<div class="top container-fluid baseWidth" style="text-align:center">
  <div class="alert alert-warning" role="alert">
    <B style="font-size:2rem">※ 現在、試作運用版です。UIデザインなどは後程変更します。</B>
  </div>
  <div class="row" style="margin:auto">
    <div class="col-6">
      <img v-show="this.$root.mode == 0" :src="this.$root.IMG_PATH + 'btn_mode1_on.png'" v-on:click="pushMode(0)" style="width:100%">
      <img v-show="this.$root.mode == 1" :src="this.$root.IMG_PATH + 'btn_mode1_off.png'" v-on:click="pushMode(0)" style="width:100%">
    </div>
    <div class="col-6">
      <img v-show="this.$root.mode == 1" :src="this.$root.IMG_PATH + 'btn_mode2_on.png'" v-on:click="pushMode(1)" style="width:100%">
      <img v-show="this.$root.mode == 0" :src="this.$root.IMG_PATH + 'btn_mode2_off.png'" v-on:click="pushMode(1)" style="width:100%">
    </div>
  </div>
  <br>
  <h3 class="fontBase">ゲームタイトル検索</h3>
  <input type="text" style="width:90%; margin:auto" :value="searchWord" @input="updateList" class="form-control" placeholder="ゲーム名（英語表記）を入力してください" aria-label="Game Title" aria-describedby="search_button">
  <hr style="height: 2px; background-color:white; border:none;">
  <h1 class="fontBase">{{ searchLabel }}</h1>
  <div class="row" style="display:flex; flex-wrap:wrap; justify-content:center; margin:auto">
    <game-title-row v-for="(rowData, index) in dispDataList" :key="index" v-bind:rowData='rowData'></game-title-row>
  </div>
  <button v-show="this.isNext == true" class="cusButton" type="button" v-on:click="pushNext">
    <img :src="this.$root.IMG_PATH + 'btn_showmore.png'" style="height:50px">
  </button>
  
  <transition name="fade">
    <button type="button" class="cusButton" style="position:fixed; bottom:15px; right:15px" v-if="show" @click="moveTop">
      <img :src="this.$root.IMG_PATH + 'btn_pageUp.png'" style="height:50px">
    </button>
  </transition>
</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Top',
  components: {
  },
  data() {
    return {
      langIndex : 0,
      btnOn : "btn-primary",
      btnOff : "btn-secondary",
      pageIndex : 1,
      searchWord : "",
      isNext : false,
      searchLabel : "Top Games",

      dispDataList: [],
      filterDataList : [],
      allRowDataList: [],

      show : false,
    };
  },
  mounted () {
    window.addEventListener('scroll', () => {
        console.log(window.scrollY);
        this.show = (window.scrollY > 150);

    });
  },
  methods : {
    updateList : function (e) {
      this.searchWord = e.target.value;
      if (this.searchWord == "") {
        //検索ワードなし
        this.filterDataList = this.allRowDataList;
        this.searchLabel = "Top Games (" + this.filterDataList.length + ")";
      } else {
        //検索ワードあり
        this.filterDataList = [];
        for (let i = 0; i < this.allRowDataList.length; i++) {
          const gameData = this.allRowDataList[i];
          if (gameData["name"].toUpperCase().indexOf(this.searchWord.toUpperCase()) > -1) {
            //一致
            this.filterDataList.push(gameData);
          }
        }
        this.searchLabel = "Search '" + this.searchWord + "' (" + this.filterDataList.length + ")";
      }
      console.log(this.searchLabel)
      this.pageIndex = 1;
      this.dispList();
    },
    dispList : function () {
      let max = 40 * this.pageIndex;
      if (max > this.filterDataList.length) {
        max = this.filterDataList.length;
        this.isNext = false;
      } else {
        this.isNext = true;
      }

      this.dispDataList = [];
      for (let i = 0; i < max; i++) {
        this.dispDataList.push(this.filterDataList[i]);          
      }
    },
    pushNext : function () {
      this.pageIndex = this.pageIndex + 1;
      this.dispList();
    },
    moveTop : function () {
      window.scrollTo(0, -100);   // スクロール位置を移動
    },
    pushMode : function (val) {
      this.$root.mode = val;
      localStorage.mode = val;
    },
    pushLang : function (index) {
      this.langIndex = index;
      console.log(index);
    },
    getGameList : function () {
      axios.get(this.$root.SERVER_PATH + 'getGames')
      .then((response) => {
        let resData = response.data;
        console.log(resData);

        this.allRowDataList = [];
        this.allRowDataList = resData["retList"];
        this.filterDataList = this.allRowDataList;
        this.searchLabel = "Top Games (" + this.filterDataList.length + ")";
        this.pageIndex = 1;

        let count = 0;
        for (let index = 0; index < this.allRowDataList.length; index++) {
          count += this.allRowDataList[index]["order"];
        }
        console.log(count + "ハイシ");

        this.dispList();

      }).catch((error) => {
        console.log(error.response);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getGameList();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.getGameList();
    });
  },
}
</script>
